@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap);
.topbar {
    background: linear-gradient(270deg, #E71215 19.54%, #941014 100%);
    padding: 5px 0 ;
}

.topbar .topbar-container {
    width: 90%;
    margin: 0 auto;
    display: flex;
    align-items: center;
}
.topbar .topbar-container .topbar-logo {
    width: 50%;
}
.topbar .topbar-container .topbar-icon-logout {
    width: 50%;
    text-align: right;
}
.topbar .logo {
    width: 29px;
    height: auto;
}

@media(min-width: 750px) {
    .topbar {
        padding: 10px 0;
    }
    .topbar .logo {
        width: 34px;
        height: auto;
    }
}
.form-container {
    max-width: 440px;
    margin: 0 auto;
    padding: 30px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 35px rgba(0, 0, 0, 0.1);
}

.form {
    display: flex;
    flex-direction: column;
}

.form h3 {
    margin: 0 0 25px;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
    color: #505050;
    text-align: center;
}
.form .form-box {
    width: 100%;
    position: relative;
}
.form input {
    width: 100%;
    padding: 11px 20px 11px 50px;
    border-radius: 50px;
    margin-bottom: 25px;
}
.form .input-text {
    font-size: 14px;
    outline: none;
    background: #e8e8e8;
    color: #616161;
    border: 1px solid #e8e8e8;
}
.form .button-submit {
        background: #00a875;
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
        border: none;
        color: #fff;
        padding: 12px 50px 11px 50px;
        letter-spacing: 1px;
        font-size: 15px;
        font-weight: 400;
        border-radius: 50px;
}
.form .form-group-btn {
    text-align: center;
}
.form p {
    color: #616161;
    margin-bottom: 0;
    margin-top: 30px;
    text-align: center;
    font-size: 14px;
}
.form a {
    color: #616161;
    text-decoration: none;
}
.container-register-form {
    height: calc(100vh - 54px);
    display: flex;
    align-items: center;
}

.content-register-container {
    width: 90%;
    margin: 0 auto;
}

.welcome-container {
    width: 100%;
    display: inline-block;
    align-items: flex-end;
    text-align: right;
    margin-top: 20px;
}
.welcome-container .welcome-title {
    font-size: 12px;
    text-align: right;
}

@media(min-width:750px) {
    .welcome-container {
        margin-top: 40px;
    }
    .welcome-container .welcome-title {
        font-size: 14px;
    }
}
.container-title-process-dash {
    display: flex;
    align-items: flex-end;
    margin-top: 30px;

}
.title-process {
    width: 90%;
}

.title-process .title {
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 700;
}

.icon-process {
    width: 50%;
    text-align: right;
}

.hr {
    margin-top: 15px;
    background: #DFD7D7;
    width: 100%;
    height: 1px;
    
}

@media(min-width:750px) {
    .title-process .title {
        font-size: 20px;
    }
}
.btn-light {
    border: none !important;
    background: rgba(52, 152, 219) !important;
    color: #fff !important;
    padding: 12px 50px 11px 50px !important;
    letter-spacing: 1px;
    font-size: 15px;
    font-weight: 400;
    border-radius: 50px !important;
}
.container-title-process {
    display: flex;
