.h4 {
    font-size: 16px !important;
}
@media(min-width:750px) {
    .h4 {
        font-size: 20px !important;
    }
}
.ctn-process-card {
    display: grid;  
    grid-template-columns: 100%;
    grid-row-gap:20px;
    row-gap:20px;  
    margin-top: 20px;
}

.form-container-process {
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    padding: 20px;
}

.container-process {
    display: flex;
    flex-direction: column;
    line-height: 24px;
}
.container-process h3:nth-child(1) {
    font-weight: 700;
    font-size: 16px;
}
.container-process h3:nth-child(2) {
    font-size: 14px;
    font-weight: 400;
}
.container-process h3:nth-child(3) {
    font-size: 14px;
    font-weight: 400;
}

.form-container-process:hover {
    background: #E5E5E5;
}

@media(min-width:750px) {
    .ctn-process-card {
        grid-template-columns: 49% 49%; 
        grid-column-gap:2%; 
        -webkit-column-gap:2%; 
                column-gap:2%; 
        grid-row-gap:25px; 
        row-gap:25px;   
        margin-top: 40px;
    }
    .container-process {
        line-height: 28px;
    }
    .container-process h3:nth-child(1) {
        font-size: 18px;
    }
    .container-process h3:nth-child(2) {
        font-size: 16px;
    }
    .container-process h3:nth-child(3) {
        font-size: 16px;
    }
    
    
}
