.form-container {
    max-width: 320px;
    margin: 0 auto;
    padding: 30px;
    background: #fff;
    border-radius: 5px;
    box-shadow: 0 0 35px rgba(0, 0, 0, 0.1);
}

.form {
    display: flex;
    flex-direction: column;
}
.login-form {
    display: flex;
    flex-direction: column;
}

.login-form h3 {
    margin: 0 0 25px;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Open Sans', sans-serif;
    color: #505050;
    text-align: center;
}
.login-form .login-form-box {
    width: 100%;
    position: relative;
}
.login-form input {
    background: rgba(255, 255, 255, 0.47);
    border-radius: 50px;
    width: 100%;
    padding: 11px 20px 11px 50px;
    margin-bottom: 25px;
    border:none;
    font-size: 16px;
    color:#fff;
}
.login-form .login-input-text {
}
.login-form .login-button-submit {
    border: 3px solid #FD252C;
    background: rgba(253, 37, 44, 0.1);
    color:#fff;   
    padding: 12px 50px 11px 50px;
    letter-spacing: 1px;
    font-size: 15px;
    font-weight: 400;
    border-radius: 50px;
}
.login-form .login-form-group-btn {
    text-align: center;
}
.login-form p {
    color: #616161;
    margin-bottom: 0;
    margin-top: 30px;
    text-align: center;
    font-size: 14px;
}
.login-form a {
    color: #616161;
    text-decoration: none;
}
.text-register {
    line-height: 22px;
}
.text-register p{
    font-size: 16px;
    color: #fff;
}
.text-register a {
    color: #fff;
    font-weight: 500;
    margin-bottom: 20px;
}

.select {
    border-radius: 5px;
    padding: 8px 30px;
}

.register-form .button-submit {
    border: none !important;
    background: rgba(52, 152, 219) !important;
    color:#fff;   
    padding: 12px 50px 11px 50px !important;
    letter-spacing: 1px;
    font-size: 15px;
    font-weight: 400;
    border-radius: 50px;
}
@media(min-width:750px) {
    .form-container {
        max-width: 440px;
    }
    
}